/* import font*/
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */

* {
	margin: 0;
	font-family: Quicksand;
	color: #333;
	background-color:#F5F5F4
}
.navbar {
	padding: 20px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	max-width: 800px;
	margin: 0 auto;
	border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
	color:#D90368;
}
.navbar .links {
	margin-left: auto;
}
.navbar a {
	margin-left: 16px;
	text-decoration: none;
	padding: 6px;
}
.navbar a:hover {
	color: #f1356d;
}
.content {
	max-width: 800px;
	margin: 40px auto;
	padding: 20px;
}

/* blog previews */
.blog-preview {
	padding: 10px 16px;
	margin: 20px 0;
	border-bottom: 1px solid #fafafa;
}
.blog-preview:hover {
	box-shadow: 1px 3px 5px rgba(0, 0, 0, .1);
}
.blog-preview h2 {
	font-size: 20px;
	color: #f1356d;
	margin-bottom: 8px;
}
.blog-preview a {
	text-decoration: none;
}

/* create New blog form */
.create {
	max-width: 400px;
	margin: 0 auto;
	margin-bottom: 30px;
}
.create label {
	text-align: left;
	display: block;
}
.create input,
.create textarea,
.create select {
	width: 100%;
	padding: 6px 10px;
	margin: 10px 0;
	border: 1px solid #ddd;
	box-sizing: border-box;
	display: block;
}
.create button {
	background: #f1356d;
	color: #fff;
	border: none;
	padding: 10px 20px;
	cursor: pointer;
	border-radius: 10px;
	box-shadow: -2px 2px 2px 0.5px rgba(0, 0, 0, .1);
}
